.App {
  max-width: 1200px;
  margin: 0 auto;
  padding: 20px;
}

.App-header {
  background-color: #1b396e;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: flex-start;
  font-size: calc(10px + 2vmin);
  color: white;
}

body {
  background-color: #f0f0f0;
  font-family: 'Arial', sans-serif;
}

.form-container {
  width: 100%;
  max-height: 0;
  overflow: hidden;
  transition: max-height 0.3s ease-out;
}

.form-container.show {
  max-height: 1000px;
  transition: max-height 0.5s ease-in;
}

form {
  display: flex;
  flex-direction: column;
  width: 100%;
}

input, textarea {
  margin-bottom: 10px;
  padding: 10px;
  font-size: 16px;
}

button {
  background-color: #1fa13c;
  border: none;
  color: white;
  padding: 7px 50px;
  text-align: center;
  text-decoration: none;
  display: inline-block;
  font-size: 12px;
  margin: 4px 2px;
  cursor: pointer;
  border-radius: 4px;
  transition: background-color 0.3s;
}

button:hover {
  background-color: #205e23;
}

.png-views {
  width: 100%;
}

.search-container {
  background-color: #f9f9f9;
  padding: 15px;
  border-radius: 4px;
  margin-bottom: 20px;
}

.png-list {
  list-style-type: none;
  padding: 0;
  width: 100%;
}

.png-item {
  background-color: #3a3f4b;
  margin-bottom: 5px;
  padding: 5px;
  border-radius: 5px;
  text-align: left;
  font-size: 16px;
  width: 100%;
  box-sizing: border-box;
}

.png-header {
  cursor: pointer;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.png-header span {
  word-break: break-word;
  flex: 1;
}

.png-description {
  margin-top: 5px;
  padding: 2px;
  background-color: #2c3038;
  border-radius: 20px;
  font-size: 16px;
  width: 100%;
  box-sizing: border-box;
}

.png-actions {
  margin-top: 10px;
  display: flex;
  justify-content: flex-start;
  flex-wrap: wrap;
}

.png-actions button {
  margin-right: 10px;
  margin-bottom: 5px;
}

.dead .png-header span {
  text-decoration: line-through;
}

.view-buttons {
  display: flex;
  flex-wrap: wrap;
}

.view-buttons button {
  margin-right: 10px;
  margin-bottom: 10px;
}

.png-filter {
  width: 100%;
  margin-bottom: 20px;
}

.png-filter input {
  width: 100%;
  padding: 5px;
  font-size: 12px;
  margin-bottom: 5px;
}

.search-toggle {
  margin-right: 20px;
}

.search-container {
  margin-bottom: 20px;
}

.top-bar {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 20px;
}

.game-selector select, 
.game-controls input,
.small-button {
  height: 30px;
  font-size: 14px;
  padding: 0 10px;
}

.game-controls {
  display: flex;
  align-items: center;
}

.game-controls input {
  margin-right: 10px;
}

.small-button {
  margin-left: 5px;
}

.main-controls {
  display: flex;
  align-items: center;
  margin-bottom: 20px;
}